* {
  box-sizing: border-box;

}

button {
  outline: none;
  border: 0;
  cursor: pointer;
}

.navbar {
  font-size: 2rem;
  padding: 1em;
  background-color: blanchedalmond;

}

.navbar-logo {
  color: green;
  margin-right: .5em;
}

.navbar-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: .2em;
  background-color: green;
  color: white;
  border-radius: 50%;
}

.add-input {
  font-size: 2rem;
}

.add-input {
  margin-right: .2rem;
}

.add-button {
  height: 100%;
  font-size: 1.5rem;
  padding: .3em .5em;
  background-color: green;
  color: white;
}

.habit-count {
  display: inline-block;
  text-align: center;
  font-size: 2rem;
  width: 2.5rem;
  height: 2.5rem;
  line-height: 2.5rem;
  margin-left: .2em;
  margin-right: .5em;
  background-color: darkseagreen;
  color: white;
  border-radius: 50%;

}

.habit {
  font-size: 2.5rem;
  padding: .5em;
  list-style: none;
}

.habit-btn {
  font-size: 2.5rem;
  margin: 0 .1em;
  background-color: transparent;
}
.habit-btn:hover {
  opacity: 0.8;
}

.habit-increase,
.habit-decrease {
  color: goldenrod;
}

.habit-delete {
  font-size: 2rem;
  color: darkred;
}

.habits-reset {
  font-size: 1.5rem;
  padding: 0.2em 1em;
  background-color: green;
  color: white;
}